<template>
  <div class="enterUnderwritingInfoDialog">
    <p class="title"></p>
    <div class="absoluteCancelIconZone" @click="cancelBtnClick"></div>
    <input type="text" v-model="feedback" ref="input">
    <br>
    <p class="title"></p>
    <div class="absoluteCancelIconZone" @click="cancelBtnClick"></div>
    <input type="text" v-model="comment">
    <div class="buttonZone">
      <button class="cancel other-button" @click="cancelBtnClick"></button>
      <button class="ensure other-button" @click="ensureBtnClick"></button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'e-EnterUnderwritingInfoDialog',
  props: {
    visible: {
      type: Boolean,
      default: false,
    }
  },
  data() {
    return {
        feedback:'',
        comment:''
    }
  },
  methods: {
    cancelBtnClick() {
      this.$emit('handleCancel');
    },
    ensureBtnClick() {
      this.$emit('handleEnsure', {feedback:this.feedback,comment:this.comment});
    },
  },
  watch: {
    visible(newVal) {
      if(newVal) {
        this.$refs.input.focus();
      }
    }
  },
}
</script>

