<template>
  <div id="underwriting">
    <div class="tableHeader">
      <p></p>
      <p></p>
      <p></p>
      <p></p>
      <p></p>
      <p></p>
      <p></p>
    </div>
    <div class="divideLine"></div>
    <div class="emptyDiv"></div>
    <Scroll class="content2"
            :probe-type="3"
            ref="scroll">
      <order-list :list="listShow" operationText="PDF"></order-list>
      <div class="paginationZone" v-show="listShow.length !== 0">
        <a-pagination
          :total="listAll.length"
          :show-total="(total, range) => `${range[0]}-${range[1]} of ${total} items`"
          :page-size="pageSize"
          :default-current="1"
          v-model:current="currentPage"
        />
      </div>

      <empty-data-placeholder v-if="listShow.length === 0" description=""></empty-data-placeholder>
    </Scroll>

    <!--  -->
    <div class="right-absolute-flex-pass" v-show="isShowPassDialog">
      <enter-underwriting-info-dialog
        @handleCancel="hidePassDialog"
        @handleEnsure="submitUnderwriting"
        ref="passDialog"
        :visible="isShowPassDialog"></enter-underwriting-info-dialog>
    </div>
    <!--  -->
    <div class="right-absolute-flex-reject" v-show="isShowRejectDialog">
      <enter-reject-comment-dialog
        @handleCancel="hideRejectDialog"
        @handleEnsure="rejectUnderwriting"
        ref="rejectDialog"
        :visible="isShowRejectDialog"></enter-reject-comment-dialog>
    </div>

    <div class="fixedCoverPage" v-if="isShowCoverPage"></div>

    <div class="fixedFullLoading" v-show="isLoading">
      <a-space>
        <a-spin size="large"></a-spin>
      </a-space>
    </div>

    <!-- pdf -->
    <div class="fixedFullPdfReviewer" v-show="viewBoardVisible"></div>
    <div class="cancelIcon" v-show="viewBoardVisible" @click="viewBoardVisible = false"></div>
    <div class="absoluteScrollWrapper" v-show="viewBoardVisible">
      <div class="fullRelativeWrapper">
        <Scroll ref="roll2"
            class="content3">
          <information-reviewer
            class="pdf-information"
            :pdfData="activeViewItem"></information-reviewer>
          <p class="buttonZone">
            <button @click="printPDF()">PDF</button>
          </p>
        </Scroll>
      </div>
    </div>

  </div>
</template>

<script>
import Scroll from '../../../components/common/scroll/Scroll.vue';
import { $getAllProductInUnderwritingStatus, $submitInsuranceAmount, $rejectUnderwriting } from '../../../network/auditSystem/aboutUnderwriting';
import {$getProductByID} from '../../../network/providerTools/activity'
import OrderList from './childComponents/orderList/OrderList.vue';
import { UrlDecode } from '../../../common/utils';
import EnterRejectCommentDialog from './childComponents/enterRejectCommentDialog/EnterRejectCommentDialog.vue';
import EnterUnderwritingInfoDialog from './childComponents/enterUnderwritingInfoDialog/EnterUnderwritingInfoDialog.vue';
import EmptyDataPlaceholder from '../../../components/common/emptyDataPlaceholder/EmptyDataPlaceholder.vue';
import InformationReviewer from './childComponents/informationReviewer/InformationReviewer.vue';
import html2Canvas from "html2canvas";
import JSPDF from "jspdf";
import {$getFilterTicketAudit} from "@/pages/internal/network/auditSystem/common";
import {toBeChinese} from './toBeChinese'

export default {
  name: 'e-Underwriting',
  /**
   * 
   */
  beforeRouteEnter (to, from, next) {
    if(window.localStorage.getItem('isStaff') === 'true') {
      window.localStorage.setItem('currentIndex010003', 6);
      next();
    }
    else {
      next({
        path: '/tools/login'
      });
      window.localStorage.removeItem('list');
      window.localStorage.removeItem('order');
      window.localStorage.removeItem('currentIndex010003');
      window.localStorage.removeItem('userID');
      window.localStorage.removeItem('username');
      window.localStorage.removeItem('authToken');
      window.localStorage.removeItem('isStaff');
      window.localStorage.removeItem('isSuperUser');
      window.localStorage.removeItem('isProvider');
      window.localStorage.removeItem('providerID');
    }
  },
  /**
   * 
   */
  async mounted() {
    let data = {
      "work_line_id": 1400,
        "work_operation": 4400,
      //  "status": 3200,
      // "start_date": "2021-12",
      // "end_date": "2022-02"
    }
    this.isLoading = true;
    try {

      const response = await $getFilterTicketAudit(data);
      console.log(response)
      const { Code, msg } = response;
      // 
      if (Code === 200 && msg === 'success') {
        console.log(response.details.tickets)
        this.listAll = response.details.tickets.reverse(); // listAll
        this.totalPage = Math.ceil(this.listAll.length / this.pageSize); // totalPage
        const tempListAll = window.JSON.parse(window.JSON.stringify(this.listAll));
        // listShow
        if (this.listShow.length === 0) {
          this.listShow = tempListAll.slice(0, this.pageSize);
        }
        // currentPagetotalPagelistAlllistShow
        if (this.currentPage === this.totalPage) {
          this.listShow = tempListAll.slice(this.pageSize*(this.currentPage-1), this.pageSize*this.currentPage)
        }
      }
      else {
        this.$toast.error(' ', 240);
        console.log(' ')
      }
    }
    catch(err) {
      this.$toast.error('  ', 240);
      console.log(' ')
    }
    this.isLoading = false;
    this.$bus.$on('viewDetailsClick',async (param) => {
      const date = +new Date();
      const {index} = param;
      //indexactiveViewItem
      console.log(this.listShow[index])
     const {id}=this.listShow[index].content
      let res= await $getProductByID({
         id
        })
      if(res.Code==200){
         this.activeViewItem=toBeChinese(res.details)
      }
      this.viewBoardVisible = true;
      setTimeout(() => {
        this.$refs.roll2.refresh();
      }, 100);
    });

    this.$bus.$on('rejectClick', (index) => {
      // console.log((this.currentPage-1) * this.pageSize + index);
      this.indexInListShow = index;
      this.activeRecordIndex = (this.currentPage-1) * this.pageSize + index;
      this.isShowRejectDialog = true;
      this.isShowCoverPage = true;
    });

    this.$bus.$on('passClick', (index) => {
      // console.log((this.currentPage-1) * this.pageSize + index);
      this.indexInListShow = index;
      this.activeRecordIndex = (this.currentPage-1) * this.pageSize + index;
      this.isShowPassDialog = true;
      this.isShowCoverPage = true;
    });
  },
  /**
   * 
   */
  unmounted() {
    this.$bus.$off('viewDetailsClick');
    this.$bus.$off('passClick');
    this.$bus.$off('rejectClick');
  },
  data() {
    return {
      listAll: [],
      listShow: [],
      pageSize: 8,
      currentPage: 1,
      totalPage: 0,
      isShowCoverPage: false,
      viewBoardVisible: false,
      x: null,
      y: null,
      // activeViewItem: {},
      activeViewItem: {
      },
      isShowPassDialog: false,
      isShowRejectDialog: false,
      activeRecordIndex: -1,
      indexInListShow: -1,

      isLoading: false,
    }
  },
  methods: {
     VMUrlDecode(str) {
      return UrlDecode(str);
    },
    hidePassDialog() {
      this.isShowCoverPage = false;
      this.isShowPassDialog = false;
      this.$refs.passDialog.message = '';
    },
    async submitUnderwriting(message) {
      const productId = this.listAll[this.activeRecordIndex].content.id;
      const id=this.listAll[this.activeRecordIndex].id;
    console.log(this.listAll)
      if(message.feedback === '') {
        this.$toast.error('', 240);
        return;
      }

    //   if(!/^[1-9]\d*\.?\d*$/.test(message.feedback)) {
    //     this.$toast.error('', 240);
    //     return;
    //   }

      try {
        const requestPayload = {
          id,
          review_comment:message.comment,
          content:{
                id:productId,
                insurance_feedback:message.feedback,
            }
        }
        console.log(requestPayload)
        const response = await $submitInsuranceAmount(requestPayload);
        console.log("insurance_fee",response)
        if(response.Code === 200 && response.msg === 'success') {
          this.$toast.success('', 240);
          this.listAll.splice(this.activeRecordIndex, 1);
          this.listShow.splice(this.indexInListShow, 1);
        }
        else {
          this.$toast.error('', 240);
        }

        this.isShowCoverPage = false;
        this.isShowPassDialog = false;
        this.$refs.passDialog.message = '';
      }
      catch(err) {
        this.$toast.error('', 240);
        this.isShowCoverPage = false;
        this.isShowPassDialog = false;
        this.$refs.passDialog.message = '';
      }
    },
    hideRejectDialog() {
      this.isShowCoverPage = false;
      this.isShowRejectDialog = false;
      this.$refs.rejectDialog.message = '';
    },
    async rejectUnderwriting(message) {
      // message
      this.isShowCoverPage = false;
      this.isShowRejectDialog = false;
      this.$refs.rejectDialog.message = '';
      const productId = this.listAll[this.activeRecordIndex].content.id;
      try {
        const requestPayload = {
          id: productId
        }
        const response = await $rejectUnderwriting(requestPayload);
        if(response.Code === 200 && response.msg === 'success') {
          this.$toast.success('', 240);
          this.$set(this.listAll[this.activeRecordIndex], 'status', '2222');
          this.$set(this.listShow[this.indexInListShow], 'status', '2222');
        }
        else {
          this.$toast.error('', 240);
        }
      }
      catch(err) {
        this.$toast.error('', 240);
      }
    },
    printPDF() {
      const pdfName = +new Date();
      console.log(this.listShow)
      this.printOut(pdfName);//pdfName:pdf
      setTimeout(() => {
        this.viewBoardVisible = false;
      }, 500);
    },
    printOut(pdfName) {
      let pdfDom = document.querySelector('.pdf-information'); //
      pdfDom.style.background = "#FFFFFF";
      html2Canvas(pdfDom,{
        //,
        allowTaint: false,
        taintTest: false,
        logging: false,
        useCORS: true,
        dpi: window.devicePixelRatio*4, //DPI 
        scale:4 //
      }).then((canvas)=>{
            // PDF
            var pdf = new JSPDF("p", "mm", "a4") // A4
            var ctx = canvas.getContext("2d")
            console.log(canvas.height)
            var a4w = 170; var a4h = 257 // A4210mm x 297mm20mm170x257
            var imgHeight = Math.floor(a4h * canvas.width / a4w) // A4
            var renderedHeight = 0

            while (renderedHeight < canvas.height) {
                var page = document.createElement("canvas")
                page.width = canvas.width
                page.height = Math.min(imgHeight, canvas.height - renderedHeight)// 

                // getImageDatacanvas
                page.getContext("2d").putImageData(ctx.getImageData(0, renderedHeight, canvas.width, Math.min(imgHeight, canvas.height - renderedHeight)), 0, 0)
                pdf.addImage(page.toDataURL("image/jpeg", 1.0), "JPEG", 10, 10, a4w, Math.min(a4h, a4w * page.height / page.width)) // 10mm

                renderedHeight += imgHeight
                if (renderedHeight < canvas.height) { pdf.addPage() }// 
                // delete page;
            }
            //pdf
            pdf.save(`${pdfName}.pdf`)

      })
    },
  },
  watch: {
    currentPage(newVal) {
      const tempListAll = window.JSON.parse(window.JSON.stringify(this.listAll));
      this.listShow = tempListAll.slice(this.pageSize*(newVal-1), this.pageSize*newVal);
      this.$refs.scroll.scrollTo(0, 0, 400);
      setTimeout(() => {
        this.$refs.scroll.refresh();
      }, 200);
    },
  },
  components: {
    Scroll,
    OrderList,
    EnterRejectCommentDialog,
    EnterUnderwritingInfoDialog,
    EmptyDataPlaceholder,
    InformationReviewer
  }
};
</script>

