  },
  mounted() {
    this.$nextTick(() => {

    });
  },
  methods: {
    VMUrlDecode(str) {
      return UrlDecode(str);
    },
    viewDetailsClick(e) {
      const param = {
        x: e.clientX,
        y: e.clientY,
        index: this.index,
      }
      this.$bus.$emit('viewDetailsClick', param);
    },
    setModuleName(code) {
      if(code === 1200) {
        return '';
      }
    },
    setDate(text) {
      return processDate(text);
    },
    rejectClick() {
      this.$bus.$emit('rejectClick', this.index);
    },
    async passClick() {
      this.$bus.$emit('passClick', this.index);
    },
  },
  components: {
  },
}
</script>

