<template>
  <div id="reject-reason">
    <p class="title"><span style="color: red;font-size: 23px;"></span></p>
    <div class="imageZone" @click="cancel"></div>
    <textarea name="reaason" ref="textarea" v-model="message"></textarea>
    <div class="buttonZone">
      <button class="cancel other-button" @click="cancel"></button>
      <button class="ensure other-button" @click="ensure"></button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'e-EnterRejectCommentDialog',
  props: {
    visible: {
      type: Boolean,
      default: false,
    }
  },
  data() {
    return {
      message: '',
    }
  },
  methods: {
    cancel() {
      this.$emit('handleCancel');
    },
    ensure() {
      this.$emit('handleEnsure', this.message);
    },
  },
  watch: {
    visible(newVal) {
      if(newVal) {
        this.$refs.textarea.focus();
      }
    },
  },
}
</script>

